

.DiaryList .menu_wrapper {
  margin-top: 20px;
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
}

.DiaryList .left_col select {
  margin-right: 10px;
  border: none;
  border-radius: 5px;
  background-color: #ececec;

  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;

  cursor: pointer;
  font-family: 'GangwonEduSaeeum_OTFMediumA';
  font-size: 18px;
}

.DiaryList .menu_wrapper .right_col {flex-grow: 1;}

.DiaryList .menu_wrapper .right_col button {width: 100%;}

